.Filter__label {
  width: 300px;
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 10px;
  margin-right: 60px;
  text-align: center;
}

.Filter__input {
  width: 330px;
  display: block;
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 15px;

  border: 0;
  outline: 0;
  font-size: 1.3rem;
  color: black;
  background-color: white;

  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.6);
  border-radius: 10px;
}